import React, { useEffect } from "react";
import XIcon from "@mui/icons-material/X";
import SidebarOption from "./SidebarOption";
import HomeIcon from "@mui/icons-material/Home";
import SearchIcon from "@mui/icons-material/Search";
import MailOutlineIcon from "@mui/icons-material/MailOutline";
import NotificationsNoneIcon from "@mui/icons-material/NotificationsNone";
import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";
import ListAltIcon from "@mui/icons-material/ListAlt";
import PermIdentityIcon from "@mui/icons-material/PermIdentity";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import { Button } from "@mui/material";
import "./Sidebar.css"
import { Link } from "react-router-dom";
import { useNavigate } from 'react-router-dom';


function Sidebar({isAuth}) {

  const navigate = useNavigate();

  useEffect(() => {
    if(!isAuth) {
      navigate("/login")
    }
  }, [])

  return (
    <div className="sidebar">
      {/* Icon */}
      <XIcon className="sidebar__xIcon" />

      {/* Sidebar Option */}
      <SidebarOption text="" Icon={HomeIcon} active/>
      <SidebarOption text="" Icon={SearchIcon} />
      <SidebarOption text="" Icon={NotificationsNoneIcon} />
      <SidebarOption text="" Icon={MailOutlineIcon} />
      <SidebarOption text="" Icon={BookmarkBorderIcon} />
      <SidebarOption text="" Icon={ListAltIcon} />
      <SidebarOption text="" Icon={PermIdentityIcon} />
      <SidebarOption text="" Icon={MoreHorizIcon} />
      
       {/* Tweet Button */}
        <Button variant="outlined" className="sidebar__tweet" fullWidth></Button>
        <Link to="/logout" className="logout__link"></Link>
    </div>
  );
}

export default Sidebar;
